.type4 {
    padding-top: 20px;
}
.font-title {
    padding-top: 40px;
    padding-bottom: 20px;
    text-align: center; 
    font-size: 42px; 
}
.font-title span {
    border-bottom: 1px solid #BDBDBD; 
    padding-bottom: 4px;
}
.wbp-ps {
    text-align: center;
    line-height: 1.8;
}
.img-chuki {
    margin: 0 auto;
    padding-bottom: 10px;
}
li {
    list-style: none;
    display: inline-block;
    text-align: center;
}
.social-icons {
    text-align: center;
}
.social-icons li a{
    color: #8f8f8f;
}
.icons-about a {
    border-radius: 50%;
    background: #e5e5e3;
    padding: 12px 15px 10px 15px;
    margin: 8px;
}
.icons-about a:hover {
    background: #000000;
}
@media screen and (min-width: 769px) and (max-width: 992px)
{
    .font-title {
        padding-top: 0 !important;
    }
    .social-icons {
        text-align: none !important;
    }
    .icons-about a { 
        margin: 5px;
    }
}
@media screen and (min-width: 480px) and (max-width: 768px)
{
    .img-about {
        margin: 0 auto;
    }
    .font-title {
        padding-top: 20px !important;
    }
    .icons-about a { 
        margin: 5px;
    }
}