@charset "UTF-8";
.type7 {
  padding: 30px; }

.title-h3 h3 {
  font-weight: bold;
  font-size: 28px;
  padding-bottom: 20px; }

.title-blogmember {
  color: #0787a3; }

.img-blogmem {
  display: block;
  border-radius: 4px;
  transition: border .2s ease-in-out;
  overflow: hidden; }

.img-blogmem img {
  transition-duration: 0.5s; }

.img-blogmem img:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  cursor: pointer; }

.img-blogmember {
  width: 100%; }

.faq-details .panel-heading {
  padding: 0; }

.panel-default > .panel-heading {
  background-color: transparent;
  border: medium none;
  color: #333; }

.faq-details h4.check-title a {
  color: #333;
  display: block;
  font-weight: 700;
  letter-spacing: 2px;
  margin-left: 40px;
  padding: 6px 10px;
  text-decoration: none; }

.panel-body {
  padding: 15px 15px 0px 50px; }

.faq-details h4.check-title {
  color: #444;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 0; }

.faq-details a span.acc-icons {
  position: relative; }

.faq-details a span.acc-icons::before {
  color: #333;
  content: "";
  font-family: fontawesome;
  font-size: 24px;
  height: 40px;
  left: -51px;
  line-height: 39px;
  position: absolute;
  text-align: center;
  top: -10px;
  width: 42px; }
