.image-blogheader {
    text-align: center;
}
.img-logo {
    width: 100%;
    margin: 0 auto;
}
.image-blogheader {
  display: block;
  border-radius: 4px;
  transition: border .2s ease-in-out;
  overflow: hidden;
  border: 4px solid #e7e7e7;
}
.image-blogheader img {
    transition-duration: 0.5s;
}
.image-blogheader img:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -o-transform: scale(1.2);
  -ms-transform: scale(1.2);
  cursor: pointer;
}
.blog-new-label {
    margin-top: 25px;
    margin-left: 25px;
    color: #000;
    text-align: center;
    background-color: #fff;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    padding: 0 12px;
    display: block;
    position: absolute;
    top: 0;
    left: 2;
  }
  .month-label {
    text-align: center;
    margin-top: -10px;
    margin-bottom: 5px;
  }
  .day-label {
    text-align: center;
    margin-top: 10px;
  }
.content-blog {
    padding-top: 15px;
}
.btn-future {
