.backgroud-form {
  background: #f4f4f4;
  margin: -40px 0;
}
.form {
  background: rgba(19, 35, 47, 0.9);
  padding: 40px;
  max-width: 600px;
  margin: 40px auto;
  border-radius: 4px;
  box-shadow: 0 4px 10px 4px rgba(19, 35, 47, 0.3);
}
.tab-group {
  list-style: none;
  padding: 0;
  margin: 0 0 40px 0;
}
.tab a {
  font-size: 25px;
  text-decoration: none;
  color: #1ab188;
  transition: 0.5s ease;
}
.tab a:hover {
  color: #179b77;
} 
.tab-content > div:last-child {
  display: none;
}
.active a{
  color: #4cacff;
}
#signup h1 {
  text-align: center;
  color: #ffffff;
  padding-bottom: 20px;
}
#login h1 {
  text-align: center;
  color: #ffffff;
  padding-bottom: 20px;
}
.label {
  position: absolute;
  transform: translateY(6px);
  left: 13px;
  transition: all 0.25s ease;
  pointer-events: none;
  font-size: 22px;
}
.label .req {
  margin: 2px;
  color: #1ab188;
}
.label.active {
  transform: translateY(50px);
  left: 2px;
  font-size: 14px;
}
.label.active .req {
  opacity: 0;
}
.label.highlight {
  color: #ffffff;
}
.field-wrap input,
textarea {
  font-size: 2em;
  display: block;
  width: 100%;
  height: 100%;
  padding: 10px 10px;
  background: none;
  background-image: none;
  border: 1px solid #a0b3b0;
  border-radius: 0;
  transition: border-color 0.25s ease, box-shadow 0.25s ease;
}
input:focus,
textarea:focus {
  outline: 0;
  border-color: #1ab188;
}
.field-wrap {
  position: relative;
  margin-bottom: 40px;
}
.top-row:after {
  content: "";
  display: table;
  clear: both;
}
.top-row > div {
  float: left;
  width: 48%;
  margin-right: 4%;
}
.top-row > div:last-child {
  margin: 0;
}
.button {
  border: 0;
  outline: none;
  border-radius: 0;
  padding: 15px 0;
  font-size: 2rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  background: #1ab188;
  transition: all 0.5s ease;
  -webkit-appearance: none;
}
.button:hover,
.button:focus {
  background: #179b77;
}
.button-block {
  display: block;
  width: 100%;
}
.forgot {
  margin-top: -20px;
  text-align: right;
}
