.main {
    position: relative;
    z-index: 1;
}
.main:before {
    background-image: url('images/shopreview1.jpg');
    background-position: center center;
    background-size: cover;
    height: auto;
    left: 0;
    min-height: 100%;
    background-color: rgba(0,0,0,0.6);
    min-width: 100%;
    position: absolute;
    overflow: hidden;
    width: auto;
    content: " ";
    opacity: .4; 
    z-index: -1;
  }
  .content {
    margin-top: 30px;
  }
  .motto {
    font-size: 25px;
    color: #000;
    text-shadow: 0 1px 4px rgba(0,0,0,0.33);
    text-align: center;
  }
  .cursive {
    margin-top: 10px;
    margin-bottom: 50px;
    color: #000;
    text-align: center;
    text-shadow: 0 0 10px rgba(0,0,0,0.33);
    z-index: 3;
    font-size: 30px;
  }
  .img-reviews {
    overflow: hidden;
    border-radius: 50%;
    width: 200px;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .reviews {
    margin-top: 15px;
    color: #000;
    position: relative;
    font-size: 18px;
    text-align: center;
  }
  .color-reviews {
    color: red;
  }
  .hide {
    display: none;
  }
  @media screen and (max-width: 575px) {
    .main {
      height: 300px;
      left: 0;
      width: 100%;
    }
    .cursive {
      font-size: 25px;
      margin-bottom: 130px;
    }
    .motto {
      font-size: 20px;
    }
    .img-reviews {
      overflow: hidden;
      border-radius: 50%;
      width: 100px;
    }
  }
  